:root {
  --white: #ffffff;
  --floralwhite: #fffaf0;
  --blanchedalmond: #ffebcd;
  --gray97: #f7f7f7;
  --gray63: #a1a1a1;
}

* {
  font-family: Arial, Helvetica, sans-serif;
  box-sizing: border-box;
}

.container {
  background-color: var(--gray97);
  max-width: 1000px;
  min-height: 100vh;
  margin: 30px auto;
  padding: 30px;
  border-radius: 20px;
}

h1, h2, h3, p {
  text-align: center;
}



form {
  display: flex;
  width: 100%;
  justify-content: center;
}

input {
  border: 1px solid var(--gray97);
  padding: 10px;
  font-size: 16px;

}
input:focus {
  outline: none;
}

.form-control {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  width: 400px;
}

.btn {
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  cursor: pointer;
background-color: var(--floralwhite);
}

.recipes {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
}

.card {
  width: 200px;
  background-color: var(--white);
  margin: 10px;
  border-radius: 5px;
  padding-bottom: 10px;
}

.card-image {
  width: 100%;
}

.card-body {
  padding: 10px;
  text-align: center;
}

.category {
  color: var(--gray63);
}

a {
  background-color: var(--blanchedalmond);
  color: inherit;
  padding: 10px;
  border-radius: 10px;
  text-decoration: none;
}
